import React, { Component } from 'react';
import {Button,FormGroup,Label,Input,Table} from  'reactstrap'
import api from './helper';
import './App.css';


class App extends Component {

    constructor(props){
      super(props)
      this.state = {
        dataGuest : [],
        fullName : '',
        company : '',
        id: '',
        edit: false
      }
      // this.handleSubmit = this.handleSubmit.bind(this)
      // this.handleChange = this.handleChange.bind(this)
    }
    async handleChange(event) {
      this.setState({[event.target.name]: event.target.value});
      // console.log(event.target.value)
    }
    async handleSubmit(){
        let data = {
          fullName : this.state.fullName,
          company : this.state.company
        } 
        console.log(data)
       
        // console.log(data)
        await api.post('/guest',data)
        this.loadData()
       
    }
   async handleDelete(id){
      // alert('hola')
      const hapus = window.confirm("Press a button!");
      console.log(hapus)
      if(hapus){
        await api.delete(`/guest/${id}`)
        this.loadData()
      }
      
      // this.setState()

    }
    async handleEdit(id){
      // alert('edit')
      const guest  = this.state.dataGuest.find(guest=>guest.id===id)

      this.setState({
        fullName: guest.fullName,
        company: guest.company,
        edit: !this.state.edit,
        id: id
      })

    }

    async handleEditSave(id){
        await api.put(`/guest/${id}`,{
          fullName: this.state.fullName,
          company: this.state.company
        })

        this.setState({
          fullName: '',
          company: "",
          edit: !this.state.edit,
        })
        this.loadData()
    }


    async loadData(){
      const {data} = await api.get('/guest')
      console.log(data)
      this.setState({dataGuest : data})
    }
    componentDidMount(){
        this.loadData()
    }






  render() {
    console.log(this.state.dataGuest)
    return (
      <div className="container">
            <h1 className="text-center">App</h1>
            <FormGroup>
              <Label for="exampleEmail">Nama Lengkap</Label>
              <Input type="email" name="fullName" id="exampleEmail" value={this.state.fullName} onChange={(e)=>this.handleChange(e)} placeholder="input nama" />
            </FormGroup>
            <FormGroup>
              <Label for="exampleEmail">Company</Label>
              <Input type="email" name="company" id="exampleEmail" value={this.state.company} onChange={(e)=>this.handleChange(e)} placeholder="input perusahaan" />
            </FormGroup>
            {this.state.edit ? <Button color="primary" onClick={()=>this.handleEditSave(this.state.id)}>Edit</Button> : <Button color="primary" onClick={()=>this.handleSubmit()}>Save</Button>}
            
            

            <Table>
              <thead>
                <tr>
                  <th>Id</th>
                  <th>Full Name</th>
                  <th>Company</th>
                  <th>Actions</th>
                </tr>
              </thead>
              <tbody>
                {this.state.dataGuest.map(member=>(
                 
                  <tr key={member.id}> 
                    <th scope="row">{member.id}</th>
                    <td>{member.fullName}</td>
                    <td>{member.company}</td>
                    <td><Button className="btn-danger" onClick={()=>this.handleDelete(member.id)}>Delete</Button> ||<Button className="btn-warning" onClick={()=>this.handleEdit(member.id)}>Edit</Button> </td>
                  </tr>
                ))}
              </tbody>
            </Table>
      
      </div>
    );
  }
}

export default App;
